    onsubmit(){
      console.log(this.selectedImgs)
      // this.imgSrc = [];
      for(let i = 0; i<this.selectedImgs.length;i++){
        // console.log(this.selectedImgs)
        var fileName = this.selectedImgs[i]['name'].split('.').slice(0,-1).join('.') + '_' +  new Date().getTime();
        var filePath = `${this.curObj._id}/${this.route.snapshot.params.id}/${fileName}`;
        const fileRef = this.storage.ref(filePath)
        this.storage.upload(filePath, this.selectedImgs[i]).snapshotChanges().pipe(
          finalize(()=>{  
              fileRef.getDownloadURL().subscribe((url)=>{
                this.auth.addImg(this.curObj._id,this.route.snapshot.params.id, url).subscribe((res)=>{
                  console.log(res);
                })
                if(this.curObj.mainImg == undefined){
                    this.auth.setMainImg(url).subscribe((res)=>{
                    })
                }
                // console.log(url);
                // this.imgSrc.push(url);
              })
          })
        ).subscribe();
      }
    }
