import { Component, OnInit,AfterViewInit,ViewChild,ElementRef,OnDestroy,ChangeDetectorRef, Input } from '@angular/core';
import { AuthWedService } from 'src/app/services/auth.service';
import { Observable } from 'rxjs/Rx';
import { NgForm } from '@angular/forms';
import { environment } from 'src/environments/environment';
import { ActivatedRoute, Router } from '@angular/router';
declare const $: any;
@Component({
  selector: 'app-membershipplan',
  templateUrl: './membershipplan.component.html',
  styleUrls: ['./membershipplan.component.css']
})
export class MembershipplanComponent implements AfterViewInit, OnDestroy {
  @ViewChild('cardInfo',{static: true}) cardInfo:ElementRef;
  currentObj;
  card: any;
  cardHandler = this.onChange.bind(this);
  error: string;

  name: String;
  address: String;
  cardName: String;
  cardNumber: Number;
  cardExpiryMonth: Number;
  cardExpiryYear: Number;
  cardCvc: Number;
  savedProduct: any = {
    imagePath: '',
    title: '',
    description: '',
    price: ''
  };
  obj;
  obj_id;
  vendor_id;
  selectedProduct: any = {};
  productStore: any = [];

  cartItems: any = [];
  priceTotal:any = 0;

  checkoutInfo = [];

  elements: any;
  constructor(private router:Router,private auth:AuthWedService, private cd:ChangeDetectorRef) {
    this.currentObj = JSON.parse(localStorage.getItem('current_vendor'));
    console.log(this.currentObj)
   }
 
  ngAfterViewInit() {
    const style = {
      base: {
        lineHeight: '24px',
        fontFamily: 'monospace',
        fontSmoothing: 'antialiased',
        fontSize: '19px',
        '::placeholder': {
          color: 'purple'
        }
      }
    }
  if(elements){
    this.card = elements.create('card');
    this.card.mount(this.cardInfo.nativeElement);

    this.card.addEventListener('change', this.cardHandler);}
    // this.getCartItems();
  }
  onChange({ error }) {
    if (error) {
      this.error = error.message;
    } else {
      this.error = null;
    }
    this.cd.detectChanges();
  }
  async onSubmit(form: NgForm) {
    const name = (<HTMLInputElement>document.getElementById('name')).value;
    const address = (<HTMLInputElement>document.getElementById('address')).value;
    const additionalData = {
      name: name ? name : undefined,
      address: address ? address : undefined
    };
    let { token, error } = await stripe.createToken(this.card, additionalData);
    if (error) {
      console.log('Something is wrong:', error);
    } else {
      console.log('Success!', token);
      // ...send the token to the your backend to process the charge

      this.checkoutInfo = [this.priceTotal, name, address, token,'',this.vendor_id ];
      console.log('Checkout Array!', this.checkoutInfo);
      // Calling addVideo from savedMediaService and passing it my videoStore array.
      this.auth.makePurchase(this.checkoutInfo) .subscribe((res) => {
        this.auth.showNotification('top','right', 2, 'The payment have done successfully.');
        // this.productStore.push(res);
        // this.checkoutInfo = res;
        console.log(' ============ make purchase ============')
        if(this.priceTotal == 2100)
          this.auth.setMembership(1, this.vendor_id).subscribe((res)=>{
            console.log('sememberhip response')
            console.log(res)
            localStorage.setItem('current_vendor',JSON.stringify(res));
            this.currentObj = res;

          })
        else
        this.auth.setMembership(2, this.vendor_id).subscribe((res)=>{
          console.log('sememberhip response')
          console.log(res)
          localStorage.setItem('current_vendor',JSON.stringify(res));
          this.currentObj = res;
        })

      });
    }
    $('#myLoginModal').modal('toggle')
    this.router.navigate(['/success-page']);

    // this.router.navigate(['/success-page']);
  }
  getTotal(num:Number) {
    this.priceTotal = num;
    this.vendor_id =  this.currentObj['_id'];
}
ngOnDestroy() {
  this.card.removeEventListener('change', this.cardHandler);
  this.card.destroy();
}
}
