import { Component, OnInit, AfterViewInit } from '@angular/core';
import { AuthWedService } from 'src/app/services/auth.service';

declare interface DataTable {
  headerRow: string[];
  footerRow: string[];
  dataRows: string[][];
}

declare const $: any;
@Component({
  selector: 'app-myreview',
  templateUrl: './myreview.component.html',
  styleUrls: ['./myreview.component.css']
})
export class MyreviewComponent implements  OnInit, AfterViewInit {
  public dataTable: DataTable;
  public tArray:string[][];
  obj;
  constructor(private auth:AuthWedService){
    this.tArray = new Array();
    this.obj = JSON.parse(localStorage.getItem('current_vendor'));
    this.auth.getReviews(this.obj['_id']).subscribe((res)=>{
      console.log('>>  get Review << ')
      console.log(res)
      res.forEach(sub => {
          var temp = new Array();
          temp.push(sub['reviewName']);
          temp.push(sub['reviewEmail']);
          temp.push(sub['reviewDate']);
          temp.push(sub['reviewSubject']);
          temp.push(sub['reviewWrite']);
          temp.push(sub['reviewOverallRating']);
        this.tArray.push(temp);  
      });
     if(this.obj['reviewNum'] > 0 && this.obj['membership'] > 0){
       this.dataTable = {
        headerRow: [ 'Name','Email', 'Date', 'Subject', 'Write', 'OverallRating','Actions' ],
        footerRow: [ 'Name','Email', 'Date', 'Subject', 'Write', 'OverallRating','Actions'],
        dataRows: this.tArray
       };
     }  else{
        this.dataTable = {
          headerRow: [ 'Name','Email', 'Date', 'Subject', 'Write', 'OverallRating' ],
          footerRow: [ 'Name','Email', 'Date', 'Subject', 'Write', 'OverallRating'],
          dataRows: this.tArray
       };
     }
    },(err)=>{

    })
  }
  ngOnInit() {
      // this.dataTable = {
      //     headerRow: [ 'Name', 'Date', 'Subject', 'Write', 'OverallRating' ],
      //     footerRow: [ 'Name', 'Date', 'Subject', 'Write', 'OverallRating'],
      //     dataRows: this.tArray
      //  };  

  }
  delFunc(email,date){
    this.auth.delReview(email, date).subscribe((res) =>{
      this.auth.showNotification('top','right', 2, 'This review has just  removed.');
      this.auth.reduceNum(this.obj['_id']).subscribe((res)=>{
        this.obj['reviewNum'] -= 1;
        console.log('reduceNum')
        console.log(res)
      })
    })
  }
  ngAfterViewInit() {
    $('#datatables').DataTable({
      "pagingType": "full_numbers",
      "lengthMenu": [
        [10, 25, 50, -1],
        [10, 25, 50, "All"]
      ],
      responsive: true,
      language: {
        search: "_INPUT_",
        searchPlaceholder: "Search records",
      }

    });

    const table = $('#datatables').DataTable();

    // Edit record
    // table.on('click', '.edit', function(e) {
    //   let $tr = $(this).closest('tr');
    //   if ($($tr).hasClass('child')) {
    //     $tr = $tr.prev('.parent');
    //   }

    //   var data = table.row($tr).data();
    //   alert('You press on Row: ' + data[0] + ' ' + data[1] + ' ' + data[2] + '\'s row.');
    //   e.preventDefault();
    // });

    // Delete a record
    table.on('click', '.remove', function(e) {
      const $tr = $(this).closest('tr');
      table.row($tr).remove().draw();
      e.preventDefault();
    });

    //Like record
    // table.on('click', '.like', function(e) {
    //   alert('You clicked on Like button');
    //   e.preventDefault();
    // });

    $('.card .material-datatables label').addClass('form-group');
  }
}
